import React from "react";
import AppHeader from '../app-header';
import SearchPanel from "../search-panel";
import PostStatusFilter from "../post-status-filter";
import PostList from "../post-list";
import PostAddForm from "../post-add-form";

import './app.css'


const App = () => {

    const data = [
        {id:'as', label: ',         .', important: true},
        {id:'df', label: '          .', important: false},
        {id:'gh', label: '     ,      ,   .', important: false},
        {id:'jk', label: '  ,    ...   .', important: false},
        {id:'lz', label: '        ,         .', important: false},
        {id:'xc', label: '  ,     .        ', important: false},
        {id:'vb', label: ' ,      .', important: false}
    ];

    return (
        <div className="app">
            <AppHeader />
            <div className="search-panel d-flex">
                <SearchPanel />
                <PostStatusFilter/>                
            </div>
            <PostList posts={data}/>
            <PostAddForm/>
        </div>
    )
}

export default App;